<template>
  <v-row>
    <v-dialog v-model="dialog" persistent height="100vh" width="80vh">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          fab
          dark
          color="rgba(39,55,70,1)"
          fixed
          right
          bottom
          v-bind="attrs"
          v-on="on"
        >
          <v-icon size="23" color="#D5DBDB" dark>add</v-icon>
        </v-btn>
      </template>
      <v-card color="#F2F4F4" class="white--text" v-if="dialog">
        <v-container>
          <v-card-title class="text-h5" style="color: #273746">
            Mi nuevo cliente
          </v-card-title>
          <v-card-text style="color: #273746">Datos Generales</v-card-text>

          <v-form ref="form">
            <v-text-field
              outlined
              v-model="tradename"
              label="Nombre Comercial"
              required
            ></v-text-field>

            <v-select
              outlined
              v-model="municipality"
              :items="municipalityItems"
              label="Municipio"
              required
            ></v-select>

            <v-dialog
              ref="dialog"
              v-model="modal"
              :return-value.sync="date"
              persistent
              width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  outlined
                  v-model="newDate"
                  label="Fecha"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker v-model="newDate" scrollable>
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="modal = false">
                  Cancel
                </v-btn>
                <v-btn text color="primary" @click="$refs.dialog.save(date)">
                  OK
                </v-btn>
              </v-date-picker>
            </v-dialog>

            <v-text-field
              outlined
              v-model="whoAttendedUs"
              label="¿Quien nos atendió?"
              required
            ></v-text-field>

            <v-select
              disabled
              outlined
              v-model="typeOfSetup"
              :items="typeOfSetupItems"
              label="Tipo de dictamen"
              required
            ></v-select>
          </v-form>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="#273746" text @click="dialog = false">
              Cancelar
            </v-btn>
            <v-btn outlined color="#273746" @click="create()"> Crear </v-btn>
          </v-card-actions>
        </v-container>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
export default {
  name: "NewClient",
  data() {
    return {
      date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      municipalityItems: [
        "Cozumel",
        "Isla Mujeres",
        "Benito Juárez",
        "Solidaridad",
        "Puerto Morelos",
        "Felipe Carrillo Puerto",
        "José María Morelos",
        "Lázaro Cárdenas",
        "Tulum",
        "Othón P. Blanco",
        "Bacalar",
      ],
      typeOfSetupItems: ["Baja tensión", "Media tensión", "Tableros"],

      modal: false,
      dialog: false,
      tradename: "",
      municipality: "",
      newDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      whoAttendedUs: "",
      typeOfSetup: "Tableros",
    };
  },
  methods: {
    ...mapActions({
      getItems: "customers/getAll",
      saveItem: "customers/save",
      deleteItem: "customers/delete",
    }),
    create() {
      const data = {
        //id: this.serial(),
        tradename: this.tradename,
        municipality: this.municipality,
        date: this.newDate,
        whoAttendedUs: this.whoAttendedUs,
        typeOfSetup: this.typeOfSetup,
        //Diagrama unifilar...
        section: [
          {
            tab: "Tableros",
            content: "BoardList",
          },
        ],
        boardList: {
          //Los items del tablero....
          config: [
            {
              content: "ItemBasic",
              description: "Espacio de trabajo alrededor de equipo eléctrico. ",
              nom: "110-26",
              value: null,
            },
            {
              content: "ItemBasic",
              description:
                "Ubicación con respecto a material fácilmente inflamable. ",
              nom: "408-17",
              value: null,
            },

            {
              content: "ItemBasic",
              description: "Señales de advertencia contra arco eléctrico.",
              nom: "110-16",
              value: null,
            },
          ],
          //Lista de los tableros..
          data: [
            /* {
              reference: "1",
              nomenclature: "ewes",
              location: "dwdsss",
            }, */
          ],
        },
      };
      this.saveItem(data);
      this.getItems();
      this.cleanForm();
    },
    cleanForm() {
      this.dialog = false;
      this.tradename = "";
      this.municipality = null;
      this.newDate = new Date(
        Date.now() - new Date().getTimezoneOffset() * 60000
      )
        .toISOString()
        .substr(0, 10);
      this.whoAttendedUs = "";
    },
    serial() {
      var d = new Date().getTime();
      var uuid = "xxxxxxxxxxxx4xxxyxxxxxxxxxxxxxxx".replace(
        /[xy]/g,
        function (c) {
          var r = (d + Math.random() * 16) % 16 | 0;
          d = Math.floor(d / 16);
          return (c == "x" ? r : (r & 0x3) | 0x8).toString(16);
        }
      );
      return uuid;
    },
  },
};
</script>
